
//show toast in functions
function showToast(type, title, bodyMessage,vm) {
  vm.$bvToast.toast(bodyMessage, {
    title: `${title}`,
    variant: type,
    solid: true,
    toaster: `b-toaster-top-right`,
    autoHideDelay:5000
  })
}

//empty properites of given object
function removeEmptyProperties(theobj) {
  for (const [key] of Object.entries(theobj)) {
      if(theobj[key] == '' || theobj[key] == null) {
          delete theobj[key]
      }
  }
  return theobj
}

function resetProperties(theobj) {
  for (const [key] of Object.entries(theobj)) {
      if(key == 'workshops') {
        theobj[key] = []
      } else if (key == 'gallery') {
        theobj[key] = []
      } else if (key == 'medical_services') {
        theobj[key] = []
      } else {
        theobj[key] = ''
      }
  }
  return theobj
}

function imgUpload(receviedfile) {
  var reader = new FileReader();
  var resault 
  reader.onload = function(e) {
    resault =  e
  };
  reader.onerror = function(error) {
      alert(error);
  };
  reader.readAsDataURL(receviedfile);      
  return resault
}

//make all orders in one 

module.exports = {showToast, removeEmptyProperties, imgUpload, resetProperties}