<script>
import axios from "axios"

import { resetProperties, showToast } from '@/assets/js/index';
export default {
    name:"EditOrder",
    data() {
        return {
            
      statusOptions:[
        {
          label:"Review",
          value:"review"
        },
        {
          label:"Pending Payment",
          value:"pending_payment"
        },
        {
          label:"Processing",
          value:"processing"
        },
        {
          label:"Shipped",
          value:"shipped"
        },
        {
          label:"Completed",
          value:"completed"
        }
      ],
            info:{
                
            },

            users:[],
            loading:{
                submit:false,
                price:false,
                main: true
            }
        }
    },
    computed:{
        countries() {
            return this.$store.state.countries
        },
        states() {
            return this.$store.state.states
        },
        cities() {false
            return this.$store.state.cities
        }
    },
    methods:{

        getUsers() {
            axios.get('/admin/v1/users')
            .then((res) => {
                this.users = res.data.data
            })
            .catch((error) => {
                console.error(error)
            })
        },
        submit() {
 
            let formData = new FormData();
            // formData.append("image_path", this.$refs.image.files[0])
            // formData.append("title", this.info.title)
            // formData.append("quantity", this.info.quantity)
            // formData.append("price", this.info.price)
            // formData.append("description", this.info.description)
            // formData.append("city_id", this.info.city_id)
            // formData.append("country_id", this.info.country_id)
            // formData.append("state_id", this.info.state_id)
            // formData.append("status", this.info.status)
            // formData.append("first_name", this.info.recipient[0].first_name)
            // formData.append("last_name", this.info.recipient[0].last_name)
            // formData.append("address", this.info.recipient[0].address)
            // formData.append("phone", this.info.recipient[0].phone)
            
            let editedItem = {
                price:this.info.price,
                title:this.info.title,
                quantity:this.info.quantity,
                description:this.info.description,
                city_id:this.info.city_id,
                country_id:this.info.country_id,
                state_id:this.info.state_id,
                status:this.info.status,
                first_name:this.info.recipient[0].first_name,
                last_name:this.info.recipient[0].last_name,
                address:this.info.recipient[0].address,
                phone:this.info.recipient[0].phone
            }
            
            this.loading.submit = true
            axios.put(`/admin/v1/orders/${this.$route.params.id}`, editedItem)
            .then(res=>{
                console.log(res)
                this.loading.submit = false;
                showToast('success', 'Success', 'Order has been created successfully!', this)
                //this.info = resetProperties(this.info)
               
            })
            .catch(err=>{
                this.loading.submit = false;
                for(let error in err.response.data.errors) {
                    showToast('danger', `${error}`, `${err.response.data.errors[error][0]}`, this)
                }
                console.log(err.response)
            })
        }
    },
    watch:{
        'info.country_id': function() {
            //to prevent this function in reset mode
            if(this.info.country_id) {
                this.$store.dispatch('getStates', this.info.country_id);
                //get selected country
                let selectedCountry = this.countries.find(country=>{
                    return country.id == this.info.country_id
                })
                this.info.phone_code = selectedCountry.phonecode
            }

        },
        'info.state_id':function() {
            //to prevent this function in reset mode
            if(this.info.state_id) {
                this.$store.dispatch('getCities', this.info.state_id)
            }
        }
    },
    mounted() {
       this.$store.dispatch('getCountries') // to get countries
       //this.getUsers() // to get users
    
        //get order details
        this.loading.main = true
        axios.get(`/admin/v1/orders/${this.$route.params.id}`)
        .then(res=>{
            this.loading.main = false
            this.info = res.data.data
        })
        .catch(()=>{
            this.loading.main = false
        })
        
    }
}
</script>
